import React, { useEffect, useState } from 'react'
import { APIGetProducts } from '../../api'
import { Product } from '../Product/Product'
import './productList.scss'

export const ProductsList = () => {
  const [products, setProducts] = useState([])
  console.log('asdasd', products);

  // TODO коректно ли я получаю данные при запросе. Кажется выглядит немного коряво, что я then вызываю в компоненте...
  useEffect(() => {
    APIGetProducts().then((data) => setProducts(data))
  }, [])

  // TODO добавить добавление товара в корзиру при нажатии на кнопку "добауить в корзину"
  // TODO посмотри пожалуйста, насколько удобно добавляется подгузка картинок в товары

  return (
    <div className="product-list">
      <h2 className="product-list__title">Хиты продаж</h2>
      <div className="product-list__content">
        {products &&
          products.map((card) => {
            return <Product key={card.id} card={card} />
          })}
      </div>
    </div>
  )
}
