import { createSlice } from '@reduxjs/toolkit'

// TODO для примера. хранение товара в редаксе. хотя я получаю их сразу на странице с сервера. 
// TODO кажется забыл как делать санки. когда будем пилить нужно будет пройтись по этой теме. 

const initialState = {
	products: [
		{ id: '1', name: 'sdfsdf', price: '100000000', title: 'товар' }
	]
}

export const productsSlice = createSlice({
	name: 'products',
	initialState,
	reducers: {
		addProduct: (state) => {
			// TODO делал для демонстрации своим "студентам"
			state.products.push({ id: '7', name: 'Стул', price: '1000', title: 'товар' })
		}
	}
})

export const { addProduct } = productsSlice.actions
export default productsSlice.reducer
